import * as React from 'react';
import { Block, Plugin } from '@ali/4ever-cangjie';
import { Heading } from '@ali/4ever-mo';
import { GroupFactory } from '@ali/4ever-bamboo';
import { HeadingGroupConfig } from '../types';

const { GroupNew } = GroupFactory;

const {
  Heading1,
  Heading2,
  Heading3,
  Heading4,
  Heading5,
  Heading6,
} = Heading;

const headingList = [
  Heading1,
  Heading2,
  Heading3,
  Heading4,
  Heading5,
  Heading6,
];

function createRenderGroup(config: HeadingGroupConfig): Plugin['renderGroup'] {

  return function renderGroup(props, _, next) {
    const {
      node,
      attributes,
      children,
      visible = true,
      controller,
      group,
      isCollapsed,
    } = props;

    if (group.type === 'heading1-group' || group.type === 'heading2-group' || group.type === 'heading3-group') {
      return (
        <GroupNew
          node={node as Block}
          attributes={attributes}
          controller={controller}
          isSelected={false}
          visible={visible}
          isCollapsed={isCollapsed}
        >
          {children()}
        </GroupNew>
      );
    }

    return next();
  };
}

export default createRenderGroup;
